export const getWeekName = () => {
  let date = new Date();
  const dias = [
    'Domingo',
    'Lunes',
    'Martes',
    'Mircoles',
    'Jueves',
    'Viernes',
    'Sbado',
  ];

  let weekNumber = date.getDay();

  let weekName = dias[weekNumber];

  return weekName;
};
